.webing-whatsapp-wrapper {
	input {
		&.invalid {
			border-color: #e74c3c;
			background: #ffffff url('../images/error-icon.svg') no-repeat center right;
		}
	}
}

.rtl {
	.webing-whatsapp-wrapper {

		.field-wrapper {
			direction: ltr;
			text-align: right;
		}

		input {
			&.invalid {
				background-position: center left;
			}
		}
	}
}